<script>
import Navbar from '../Layout/Navbar'
import ProductCard from '../Layout/ProductCard'
import InstagramProducts from '../Layout/InstagramProducts'
import MainFooter from '../Layout/MainFooter'

export default {
  name: '',
  components: {
    Navbar,
    InstagramProducts,
    ProductCard,
    MainFooter,
    
  },
  methods: {
    slideCategory(e){
      document.querySelectorAll('.dropdown-category').forEach(list => {
        list.classList.remove('active')
      })
      if(e.target.parentElement.querySelector('.dropdown-category').classList.contains('active')){
        e.target.parentElement.querySelector('.dropdown-category').classList.remove('active')
      } else{
        e.target.parentElement.querySelector('.dropdown-category').classList.add('active')  
      }
      

      e.preventDefault()
    }
  },
  created(){
    window.scrollTo(0, 0)
  },
}
</script>

<style lang="scss" scoped>
#shop-page{
  .quick-links{
    margin: 4rem 0;
  }
  .main-content{
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    aside{
      > ul{
        .dropdown{
          > p{
            color: var(--dark-c);
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 1.1rem;
            > *{pointer-events: none;}
          }
          .dropdown-category{
            overflow: hidden;
            height: 0px;
            li a {
              margin: 0.3rem 0.5rem;
              color: var(--semi-dark);
              &:before{
                content: '-';
                display: inline-block;
                margin-right: 0.4rem;
              }
            }
            &.active{
              height: auto;
            }
          }
        }
      }
      .price-filter{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .btn{
          border: 2px solid var(--red-c);
          padding: 0.5rem 1.5rem;
          font-weight: bold;
        }
      }
      .sizes, .colors{
        margin: 1rem 0;
        li{margin: 0.7rem 0; font-size: 1.1rem; input{margin-right: 0.5rem;}}
      }
    }
    .product-cards{
      display: grid;
      gap: 1rem;
      grid-template-columns: 1fr 1fr;
      ul{
        display: flex;
        justify-content: center;
        grid-column: 1/ 3;
        margin-bottom: 2rem;
        li:last-child a{
          line-height: 3rem;
        }
        li a{
          width: 2.5rem;
          height: 2.5rem;
          line-height: 2.5rem;
          text-align: center;
          border: 2px solid var(--grey-c);
          border-radius: 99rem;
          margin: 1rem 0.5rem;
          transition: var(--transit);
          &:hover{
            background-color: var(--dark-c);
            color: var(--white-c);
          }
        }
      }
    }
  }
}
@media (min-width: 768px){
  #shop-page{
    .main-content{
      grid-template-columns: 1fr 2.5fr;
    }
  }
}
@media (min-width: 991px){
  #shop-page{
    .main-content{
      grid-template-columns: 1fr 3fr;
      .product-cards{
        grid-template-columns: repeat(3, 1fr);
        ul{
          grid-column: 1 / 4;
        }
      }
    }
  }
}
</style>