<template>
  <nav id="main-nav" class="container-fluid">
    <h1 class="header-f"><router-link :to="{name: 'home-page'}">Ashion</router-link></h1>
    <span class="pointer" @click="openNav"><ion-icon name="menu-outline" size="large"></ion-icon></span>
    <div class="nav-links">
      <span class="pointer" @click="closeNav"><ion-icon name="close-circle-outline" size="large"></ion-icon></span>
      <div class="icons">
        <span class="pointer"><ion-icon name="search-outline" size="large"></ion-icon></span>
        <span class="pointer"><ion-icon name="heart-outline" size="large"></ion-icon><span>2</span></span>
        <span class="pointer"><router-link :to="{name: 'cart-page'}"><ion-icon name="document-outline" size="large"></ion-icon></router-link><span>2</span></span>
      </div>
      <h1 class="header-f"><router-link :to="{name: 'home-page'}">Ashion</router-link></h1>
      <ul>
        <li>
          <router-link :to="{name: 'home-page'}">Home</router-link>
        </li>
        <li>
          <router-link :to="{name: 'home-page'}">Women's</router-link>
        </li>
        <li>
          <router-link :to="{name: 'home-page'}">Men's</router-link>
        </li>
        <li>
          <router-link :to="{name: 'shop-page'}">Shop</router-link>
        </li>
        <li class="dropdown" @click="openDropdown">
          <span class="pointer">Pages <ion-icon name="caret-forward-outline"></ion-icon></span>
          <ul class="dropdown-menu">
            <li>
              <router-link :to="{name: 'product-details', params: {id: 1}}">Product Details</router-link>
            </li>
            <li>
              <router-link :to="{name: 'cart-page'}">Shop Cart</router-link>
            </li>
            <li>
              <router-link :to="{name: 'checkout-page'}">Checkout</router-link>
            </li>
            <li>
              <router-link :to="{name: 'blog-details'}">Blog Details</router-link>
            </li>
          </ul>
        </li>
        <li>
          <router-link :to="{name: 'blog-page'}">Blog</router-link>
        </li>
        <li>
          <router-link :to="{name: 'contact-page'}">Contact</router-link>
        </li>
      </ul>
      <p>
        <a href="#">Login</a> / <a href="#">Register</a>
      </p>
    </div>
  </nav>
</template>

<script>
export default {
  name: '',
  methods: {
    openNav(e){
      document.querySelector('.nav-links').style.cssText += 'left: 0;'
      e.preventDefault()
    },
    closeNav(e){
      document.querySelector('.nav-links').style.cssText += 'left: -400vw;'
      e.preventDefault()
    },
    openDropdown(e){
      if(!e.target.classList.contains('active')){
        document.querySelector('.dropdown-menu').style.cssText = 'height: auto; margin-top: 1rem;'
        e.target.classList.add('active')
      } else{
        document.querySelector('.dropdown-menu').style.cssText = 'height: 0px; margin-top: 0;'
        e.target.classList.remove('active')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
#main-nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  .nav-links{
    overflow-y: auto;
    transition: var(--transit);
    position: fixed;
    z-index: 6666;
    top: 0;
    left: -400vw;
    bottom: 0;
    width: 50%;
    max-width: 350px;
    padding: 1rem 2rem;
    background-color: var(--white-c);
    box-shadow: 10px 0 25px 200vw rgba(0,0,0,0.7);
    display: flex;
    flex-direction: column;
    > span{align-self: flex-end; opacity: 0.8;}
    opacity: 0.95;
    .icons{
      align-self: center;
      margin: 2rem 0;
      & > *{
        margin: 0 1rem;
        position: relative;
        > span{
          position: absolute;
          background-color: var(--dark-c);
          width: 1.5rem;
          height: 1.5rem;
          line-height: 1.5rem;
          display: inline-block;
          text-align: center;
          border-radius: 99rem;
          color: var(--white-c);
          transform: translate(-10px, -5px);
        }
      }
    }
    > ul{
      li{
        margin-bottom: 1rem;
        &.dropdown{
          .dropdown-menu{
            margin-left: 1rem;
            overflow: hidden;
            display: block;
            height: 0px;
          }
        }
      }
    }
  }
}
@media (min-width: 768px){

}
</style>