// import components
import HomePage from './components/View/HomePage'
import ShopPage from './components/View/ShopPage'
import BlogPage from './components/View/BlogPage'
import ContactPage from './components/View/ContactPage'
import ProductDetails from './components/View/ProductDetails'
import CartPage from './components/View/CartPage'
import CheckoutPage from './components/View/CheckoutPage'
import BlogDetails from './components/View/BlogDetails'

// routes
const routes = [
    {path: '/', component: HomePage, name: 'home-page'},
    {path: '/shop', component: ShopPage, name: 'shop-page'},
    {path: '/shop/:id', component: ProductDetails, name: 'product-details', props: true},
    {path: '/blog', component: BlogPage, name: 'blog-page'},
    {path: '/blog/:id', component: BlogDetails, name: 'blog-details', props: true},
    {path: '/contact', component: ContactPage, name: 'contact-page'},
    {path: '/cart', component: CartPage, name: 'cart-page'},
    {path: '/checkout', component: CheckoutPage, name: 'checkout-page'}
]

// export routes out to use
export default routes