<template>
  <section id="contact-page">
    <!-- navbar -->
    <navbar />

    <div class="container">
      <div class="quick-links">
        <p>
          <span><router-link :to="{name: 'home-page'}"><ion-icon name="home"></ion-icon> Home</router-link></span> > <span>Shop</span>
        </p>
      </div>
    </div>

    <section class="main-content container">
      <div id="contact-form">
        <div class="info">
          <h2 class="upper">Contact Info</h2>
          <h3><ion-icon name="location"></ion-icon> <span>Address</span></h3>
          <p>160 Pennsylvania Ave NW, Washington, Castle, PA 16101-5161</p>
          <h3><ion-icon name="call"></ion-icon> <span>Phone</span></h3>
          <p>125-711-811125-668-886</p>
          <h3><ion-icon name="help-buoy"></ion-icon> <span>Support</span></h3>
          <p>Support.photography@gmail.com</p>
        </div>
        <form action="/" @submit.prevent>
          <h2 class="upper">Send Message</h2>
          <input type="text" placeholder="Name">
          <input type="email" placeholder="Email">
          <input type="text" placeholder="Website">
          <textarea rows="7" placeholder="Message"></textarea>
          <input type="submit" value="Send Message" class="btn-red">
        </form>
      </div>
      <!-- map -->
      <div id="map">
        <GmapMap
          :center="{lat:10, lng:10}"
          :zoom="7"
          map-type-id="terrain"
          style="width: 500px; height: 300px"
        >
          <GmapMarker
            :key="index"
            v-for="(m, index) in markers"
            :position="m.position"
            :clickable="true"
            :draggable="true"
            @click="center=m.position"
          />
        </GmapMap>
      </div>
    </section>

    <!-- instagram products -->
    <instagram-products />

    <!-- footer -->
    <main-footer />
    
  </section>
</template>

<script>
import {gmapApi} from 'vue2-google-maps'

import Navbar from '../Layout/Navbar'
import InstagramProducts from '../Layout/InstagramProducts'
import MainFooter from '../Layout/MainFooter'

export default {
  name: '',
  components: {
    Navbar,
    InstagramProducts,
    MainFooter
  },
  created(){
    window.scrollTo(0, 0)
  },
  computed: {
    google: gmapApi
  }
}
</script>

<style lang="scss" scoped>
#contact-page{
  .quick-links{
    margin: 4rem 0;
  }
  .main-content{
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    #contact-form{
      display: flex;
      flex-direction: column;
      gap: 1.5rem;
      h2{margin-bottom: 1.5rem;}
      .info{
        h3{margin-bottom: 0.5rem;}
        h3 > *:first-child{
          color: var(--red-c);
          margin-right: 0.5rem;
          font-size: 1.5rem;
        }
        p{margin-top: 0; margin-bottom: 2rem;}
      }
      form{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        input, textarea{
          padding: 1rem 1.8rem;
          border: 1px solid var(--grey-c);
          border-radius: var(--rad);
          width: 90%;
          margin-bottom: 1rem;
          font-size: 1.1rem;
          &[type="submit"]{
            width: auto;
            font-size: 1rem;
          }
          &:focus{
            border-color: #ccc;
          }
        }
      }
    }
    #map{
      width: 100%;
      max-height: 500px;
      margin-bottom: 4rem;
    }
  }

}
@media (min-width: 768px){
  #contact-page{
    .main-content{
      grid-template-columns: repeat(2, 1fr);
      gap: 4rem;
    }
  }
}
</style>