<script>
import { Slider, SliderItem } from 'vue-easy-slider'

import Navbar from '../Layout/Navbar'
import ProductCard from '../Layout/ProductCard'
import InstagramProducts from '../Layout/InstagramProducts'
import MainFooter from '../Layout/MainFooter'


export default {
  name: '',
  components: {
    Navbar,
    ProductCard,
    Slider,
    SliderItem,
    InstagramProducts,
    MainFooter
  },
  data: function(){
    return {
      products: null,
      seconds: 59,
    }
  },
  created(){
    window.scrollTo(0, 0)
  },
  mounted: function() {
    const removeActives = () => {
      document.querySelectorAll('.new-product ul a').forEach(link => {
        link.classList.remove('active')
      })
    }
    document.querySelectorAll('.new-product ul a').forEach(link => {
      link.onclick = e => {
        removeActives()
        e.target.classList.add('active')

        e.preventDefault()
      }
    })

    setInterval(() => {
      this.seconds--
      if(this.seconds === 0){
        this.seconds = 59
      }
    }, 1000)
  }
}
</script>

