<script>
import Navbar from '../Layout/Navbar'
import ProductCard from '../Layout/ProductCard'
import InstagramProducts from '../Layout/InstagramProducts'
import MainFooter from '../Layout/MainFooter'


export default {
  name: '',
  components: {
    Navbar,
    ProductCard,
    InstagramProducts,
    MainFooter
  },
  data() {
    return {
      quantity: 1,
      favorite: false,
      
      list: [
        { backgroundColor: '#3f51b5', width: '100%', height: '100%' },
        { backgroundColor: '#eee', width: '100%', height: '100%' },
        { backgroundColor: '#f44336', width: '100%', height: '100%' },
        { backgroundColor: '#f44336', width: '100%', height: '100%' },
      ],
    }
  },
  created(){
    window.scrollTo(0, 0)
  },
  computed: {
    productId(){
      return this.$route.params.id
    }
  },
  methods: {
    reduceQ(){
      if(this.quantity !== 1)
        this.quantity--
    },
    increaseQ(){
      this.quantity++
    },
    pickedColor(e){
      document.querySelectorAll('.colors > span').forEach(color => {
        color.classList.remove('selected')
      })
      e.target.classList.add('selected')
    },
    pickedSize(e){
      document.querySelectorAll('.sizes > span').forEach(color => {
        color.classList.remove('selected')
      })
      e.target.classList.add('selected')
    },
    activeTab(e){
      document.querySelectorAll('.specs .tabs a').forEach(link => {
        link.classList.remove('active')
      })
      e.target.classList.add('active')
      document.querySelectorAll('.specs .tab').forEach(tab => {
        tab.classList.add('hide')
      })
      document.querySelectorAll('.specs .tabs a').forEach((link, idx) => {
        if(link.classList.contains('active') && idx == 0){
          document.querySelector('.specs .tab.description').classList.remove('hide')
        } else if(link.classList.contains('active') && idx == 1){
          document.querySelector('.specs .tab.specification').classList.remove('hide')
        } else if(link.classList.contains('active') && idx == 2){
          document.querySelector('.specs .tab.reviews').classList.remove('hide')
        }
      })

      e.preventDefault()
    }
  },
  mounted() {
    document.querySelectorAll('.specs .tab').forEach(tab => {
      tab.classList.add('hide')
    })
    document.querySelectorAll('.specs .tabs a').forEach((link, idx) => {
      if(link.classList.contains('active') && idx == 0){
        document.querySelector('.specs .tab.description').classList.remove('hide')
      } else if(link.classList.contains('active') && idx == 1){
        document.querySelector('.specs .tab.specification').classList.remove('hide')
      } else if(link.classList.contains('active') && idx == 2){
        document.querySelector('.specs .tab.reviews').classList.remove('hide')
      }
    })
    // console.log(this.$route.params.id)
  },
  watch: {
    productId: {
      handler(){
        window.scrollTo(0, 0)
      },
      deep: true
    }
  }
}
</script>

<style lang="scss">
.quick-links{
