    }
    .info{
      .stars > *{
        color: var(--yellow-c)
      }
      h1 > span{color: #ccc; font-weight: 400; font-size: 1.2rem;}
      .quantity{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 1rem;
        padding: 2rem 0;
        border-bottom: 1px solid var(--grey-c);
        .group-input{
          position: relative;
          display: flex;
          justify-content: space-between;
          align-items: center;
          border: 2px solid var(--grey-c);
          border-radius: 99rem;
          width: 150px;
          padding: 1rem;
          > span{
            cursor: pointer;
          }
          input{
            max-width: 50px;
            text-align: center;
          }
        }
        button{
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 0.8rem;
        }
        > span{
          border: 2px solid var(--grey-c);
          border-radius: 99rem;
          padding: 0.5rem;
          width: 2rem;
          height: 2rem;
          line-height: 2rem;
          cursor: pointer;
          text-align: center;
          > *{
            color: var(--semi-dark);
          }
          &.active > *{color: var(--red-c) !important;}
        }
      }
      > ul{
        li{
          display: flex;
          justify-content: flex-start;
          align-items: center;
          gap: 1rem;
          &:nth-child(2){
            span{
              display: inline-block;
              width: 1rem;
              height: 1rem;
              cursor: pointer;
              margin: 0 0.2rem;
              border: 2px solid transparent;
              border-radius: 99rem;
              &:nth-child(1){background-color: rgb(185, 230, 24);}
              &:nth-child(2){background-color: rgb(8, 187, 62);}
              &:nth-child(3){background-color: rgb(67, 18, 182);}
              &.selected{border-color: rgba(0,0,0,0.5);}
            }
          }
          &.sizes{
            span{cursor: pointer;}
            .selected{color: var(--red-c);}
          }
        }
      }
    }
    .specs{
      margin-bottom: 4rem;
      .tabs{
        margin: 2rem auto;
        width: 50%;
        justify-content: center;
        align-items: center;
        li a{
          margin: 0 1rem;
          color: var(--semi-dark);
          &.active{
            color: var(--dark-c);
          }
        }
      }
      .tab{
        h2{
          color: var(--semi-dark);
          font-weight: 400;
        }
        p.lead{
          font-size: 0.9rem;
        }
      }
    }
  }
  .related-products{
    margin-bottom: 4rem;
    h2{
      text-align: center;
      margin: 2rem 0;
    }
    .items{
      display: grid;
      grid-template-columns: 1fr;
      gap: 1.5rem;
    }
  }
}
@media (min-width: 768px){
  .main-content .details{
    grid-template-columns: 1fr 1fr;
    .specs{
      grid-column: 1 / 3;
    }
  }
  .main-content .related-products .items{
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 991px){
  .main-content .related-products .items{
    grid-template-columns: repeat(4, 1fr);
  }
}

</style>

