<template>
  <section id="comments">
    <ul class="info">
      <li>
        <h3>3 COMMENTS</h3>
        <button class="btn-link">Leave a Comment</button>
      </li>
    </ul>
    <!-- comment -->
    <section class="comment">
      <div class="comment-info">
        <img src='http://placeskull.com/70/70' alt='user'/>
        <div class="content">
          <h3>Brandon Kelley</h3>
          <p class="lead">Duis voluptatum. Id vis consequat consetetur dissentiet, ceteros commune perpetua mei et. Simul viderer facilisis egimus tractatos splendi.</p>
          <p class="extra">
            <span class="date"><ion-icon name="time-outline"></ion-icon> Seb 17, 2019</span>
            <span class="likes" @click="updateLikes">
              <ion-icon name="heart-outline"></ion-icon>
              <span class="count">{{likes}}</span>
            </span>
            <span class="replies"><ion-icon name="arrow-redo"></ion-icon> 1</span>
          </p>
        </div>
      </div>
      
      <div class="reply">
        <img src='http://placeskull.com/70/70' alt='user'/>
        <div class="content">
          <h3>Brandon Kelley</h3>
          <p class="lead">Duis voluptatum. Id vis consequat consetetur dissentiet, ceteros commune perpetua mei et. Simul viderer facilisis egimus tractatos splendi.</p>
          <p class="extra">
            <span class="date"><ion-icon name="time-outline"></ion-icon> Seb 17, 2019</span>
            <span class="likes" @click="updateLikes">
              <ion-icon name="heart-outline"></ion-icon>
              <span class="count">{{likes}}</span>
            </span>
            <span class="replies"><ion-icon name="arrow-redo"></ion-icon> 1</span>
          </p>
        </div>
      </div>
    </section>
    <section class="comment">
      <div class="comment-info">
        <img src='http://placeskull.com/70/70' alt='user'/>
        <div class="content">
          <h3>Brandon Kelley</h3>
          <p class="lead">Duis voluptatum. Id vis consequat consetetur dissentiet, ceteros commune perpetua mei et. Simul viderer facilisis egimus tractatos splendi.</p>
          <p class="extra">
            <span class="date"><ion-icon name="time-outline"></ion-icon> Seb 17, 2019</span>
            <span class="likes" @click="updateLikes">
              <ion-icon name="heart-outline"></ion-icon>
              <span class="count">{{likes}}</span>
            </span>
            <span class="replies"><ion-icon name="arrow-redo"></ion-icon> 1</span>
          </p>
        </div>
      </div>
    </section>
  </section>
</template>

<script>
export default {
  name: '',
  data(){
    return {
      likes: 12
    }
  },
  methods: {
    updateLikes(e){
      if(!e.target.parentElement.classList.contains('liked')){
        e.target.parentElement.querySelector('.count').textContent = Number(e.target.parentElement.querySelector('.count').textContent) + 1
        e.target.parentElement.querySelector('span > *:nth-child(1)').setAttribute('name', 'heart')
        e.target.parentElement.classList.add('liked')
      } else {
        e.target.parentElement.querySelector('.count').textContent = Number(e.target.parentElement.querySelector('.count').textContent) - 1
        e.target.parentElement.querySelector('span > *:nth-child(1)').setAttribute('name', 'heart-outline')
        e.target.parentElement.classList.remove('liked')
      }
    }
  }
}
</script>

<style lang="scss">
.info{
  margin-bottom: 2rem;
  li{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.comment{
  margin-bottom: 2rem;
}
.comment .comment-info, .comment .reply{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  gap: 1rem;
  img{width: 70px; height: 70px; object-fit: cover; border-radius: 99rem;}
  .content{
    display: flex;
    flex-direction: column;
    h3{margin: 0;}
    > p{font-size: 0.9rem; margin-bottom: 0;}
    .extra{
      margin-bottom: 0.5rem;
      > span{
        margin-right: 1rem;
        cursor: pointer;
        > *:nth-child(1){color: var(--red-c); margin-right: 0.2rem;}
      }
    }
  }
}
.comment .reply{
  margin-left: 6rem;
  margin-top: 1rem;
}
</style>