<script>
import Navbar from '../Layout/Navbar'
import InstagramProducts from '../Layout/InstagramProducts'
import MainFooter from '../Layout/MainFooter'

export default {
  name: '',
  components: {
    Navbar,
    InstagramProducts,
    MainFooter
  },
  data(){
    return {
      quantity: 1,
    }
  },
  created(){
    window.scrollTo(0, 0)
  },
  methods: {
    reduceQ(e){
      if(e.target.parentElement.parentElement.querySelector('input').value > 1){
        e.target.parentElement.parentElement.querySelector('input').value--
        const price = Number(e.target.parentElement.parentElement.parentElement.parentElement.querySelector('.price').textContent.slice(1,10000))
        const quantity = Number(e.target.parentElement.parentElement.querySelector('input').value)
        const totalPrice = e.target.parentElement.parentElement.parentElement.parentElement.querySelector('.total-price')
        totalPrice.textContent = `$ ${Number(price * quantity).toFixed(1)}`
      }
    },
    increaseQ(e){
      e.target.parentElement.parentElement.querySelector('input').value++
      const price = Number(e.target.parentElement.parentElement.parentElement.parentElement.querySelector('.price').textContent.slice(1,10000))
      const quantity = Number(e.target.parentElement.parentElement.querySelector('input').value)
      const totalPrice = e.target.parentElement.parentElement.parentElement.parentElement.querySelector('.total-price')
      totalPrice.textContent = `$ ${Number(price * quantity).toFixed(1)}`
    },
    removeItem(e){
      e.target.parentElement.parentElement.parentElement.remove()
      e.preventDefault()
    },
    refreshCart(){
      let subTotal = 0,
            total = 0;
      document.querySelectorAll('table tr td.price').forEach(price => {
        subTotal += Number(price.textContent.slice(1, 10000))
      })
      document.querySelectorAll('table tr td.total-price').forEach(totalPrice => {
        total += Number(totalPrice.textContent.slice(1, 10000))
      })
      document.querySelector('.extra .items .item .subtotal').textContent = `$ ${subTotal}`
      document.querySelector('.extra .items .item .total').textContent = `$ ${total}`
    }
  },
  mounted(){
    let subTotal = 0,
        total = 0;
    document.querySelectorAll('table tr td.price').forEach(price => {
      subTotal += Number(price.textContent.slice(1, 10000))
    })
    document.querySelectorAll('table tr td.total-price').forEach(totalPrice => {
      total += Number(totalPrice.textContent.slice(1, 10000))
    })
    document.querySelector('.extra .items .item .subtotal').textContent = `$ ${subTotal}`
    document.querySelector('.extra .items .item .total').textContent = `$ ${total}`
  }
}
</script>

